<template>

<div>
       
    <div class="panel">
       <div class="panel-heading panel-title heading" >
        課程資訊
       </div>
      
       <div class="panel-block info">
            <div class="columns is-multiline" style="width:100%">
                <div :class="columnClass">
                     課程名稱：{{ course.name }} 
                </div>
                <div :class="columnClass">
                     課程編號：{{ course.number }}
                </div>
                <div :class="columnClass">
                     上課地點：<span v-html="course.formatLocation()"></span>  
                </div>
                <div :class="columnClass">
                     聯絡電話：{{ course.center.contactInfo.tel }}
                </div>
                <div :class="columnClass">
                     上課時間：<span v-html="course.classTimesText()"></span>             
                </div>
                <div :class="columnClass">
                     課程期間：<span v-html="course.period"></span>
                </div>
                <div :class="columnClass">                        
                     課程費用：<span v-html="course.formatCostDetails()"></span>         
                </div>
                <div v-if="course.credit_count>0" :class="columnClass">                          
                     學分數：{{ course.credit_count }}       
                </div>
                <div :class="columnClass">        
                     課程時數：{{  course.hoursText }}&nbsp;{{  course.weeksText }}              
                </div>
                 <div :class="columnClass">        
                     招生對象：<span v-html="course.formatTarget()"></span>  
                </div>
            </div>

       </div>
    </div>   <!--  end panel -->
    <div v-if="isAuth">
         <profiles :user="user" @updated="setUser"></profiles>
         
         <div style="margin-top: 12px;">
           <form>
                <h2 class="title is-3">是否符合以下優惠條件</h2>
                <div class="columns is-multiline info">
                   
                    <div @click="discountSelected(item.id)" :class="columnClass" v-for="item in discounts">
                        <radio :checked="item.selected"  :val="item.id">{{ item.name }}</radio>
                    </div>    
                          
               
                </div>
               
                <hr>
                <a @click.prevent="onSubmit" class="button is-primary is-medium">確認報名</a>
                &nbsp;&nbsp;&nbsp;
                <a class="button is-outlined is-medium" @click.prevent="cancel">取消</a>
          
             </form> 
          </div>  
    </div>   
  </div>

</template>

<script>
    import Profiles from  '../../components/signup/Profiles.vue'
    export default {
        name:'SignupConfirm',
        props:['active','course','discounts'],
        components:{
           Profiles
        },
        data(){
            return{
                discountId:0,
                form: new Form({
                  signup:{
                     id:0,
                     discount:this.discountId,
                  }
                }),
                isAuth:false,
                columnClass:'column is-one-quater-mobile is-half-tablet is-half-desktop'
            }
        },
      
        watch: {
          'active': 'init'
        },
        methods:{
           
           init(){
                if(!this.active) return false
                if(!this.discounts.length)  this.onError()
                let checkAuth=this.checkAuth()
                checkAuth.then(() => {
                   this.getUser()
                }).catch(error => {
                   this.$auth.logout()
                   let returnUrl="signup/create?course=" + this.course.id
                   this.$router.push('/login?return=' + returnUrl)
                })
                
              
            },
            checkAuth(){
                return new Promise((resolve, reject) =>{
                    let authenticated=this.$auth.isAuthenticated()
                    authenticated.then(()=>{
                        resolve(true)
                         
                    }).catch(error => {
                          reject(error) 
                    })
                
                })
            },
            getUser(){
                this.isAuth=false
                let user=this.$auth.getUser()
                user.then(()=>{
                      let user_id=this.$auth.user_id()
                      let url=Helper.getUrl('/api/users/') + user_id + '/edit'          
                      axios.get(url)
                      .then(response => {
                          this.setUser(response.data.user)
                          this.isAuth=true
                      })
                      .catch(function(error) {
                         this.isAuth=false
                         this.onError()
                       
                      })
                     
                }).catch(error => {
                      this.isAuth=false
                      this.onError()
                  
                })
              
            },
            setUser(user){
               this.user =new User(user)     
            },
            discountSelected(id){
                this.discountId=id
                for (var i = 0; i < this.discounts.length; i++) {
                    let item=this.discounts[i]
                    if(item.id==id){
                      item.selected=true
                    }else{
                       item.selected=false
                    }
                }
            },
            getCourseId(){
                if(this.course) return this.course.id
                    return 0
            },
            editUserData(){
                 this.$notify.open({
                        content: '系統暫時無回應，請稍後再試',
                        type: 'danger',
                        placement: 'top-center',
                        duration: 1500,
                      })
            },
            onSubmit(){

            },
            nextStep(){
                this.$emit('nextStep')
            },
            cancel(){
                this.$emit('cancel')
            },
            onError(){
                this.$notify.open({
                        content: '系統暫時無回應，請稍後再試',
                        type: 'danger',
                        placement: 'top-center',
                        duration: 1500,
                      })
                this.cancel()
            }

        }
    }  
</script>




