<template>
<div>
    <div v-if="isReadonly" class="panel">
      <div class="panel-heading panel-title heading" >
        個人資訊
        <a class="button is-info is-outlined" @click.prevent="beginEdit">
           <span class="icon">
             <i class="fa fa-pencil fa-1" aria-hidden="true"></i>
           </span> 
           <span>修改</span>
        </a>
      </div>
       <div  class="panel-block info">
            <div class="columns is-multiline" style="width:100%">                
                <div :class="columnClass">
                     姓名：{{  user.profile.fullname }}           
                </div>
                 <div :class="columnClass">
                     性別： {{  user.genderText }}        
                </div>
                <div :class="columnClass">
                     電話： {{  user.phone }}    
                </div>
                
                <div :class="columnClass">
                     生日： {{ user.profile.dob }}  
                </div>
            </div>
       </div>
      
    </div>   <!--  end panel -->
    
   <modal :width="1000"  :show-footer="false"  :is-show="showConfirm" @close="showConfirm=false">     
        <edit-profile :id="user.id" v-if="!isReadonly" @cencelEdit="isReadonly=true"
           :edit_photo="false" @saved="onUserSaved" ></edit-profile>
  </modal>
   
         
</div>   
</template>


<script>
    import EditProfile from  '../../components/user/edit-profile.vue'
    export default {
        name:'SignupProfile',
        components:{
         'edit-profile':EditProfile,
        },
        props:['user'],
      
        data(){
            return{
                isReadonly:true,
                showConfirm:false,
                columnClass:'column is-one-quater-mobile is-half-tablet is-half-desktop'
            }
        },
        methods:{
            
            beginEdit(){
              this.showConfirm=true
                this.isReadonly=false
            },
            onSubmit(){

            },
            onUserSaved(user){
               this.$emit('updated') 
               this.init()
            }
           

        }
    }  
</script>



